.container{
    text-align: center;
}

.title{
    margin-top: 50px;
    color: white;
    font-size: 60px;
    display: flex;
    justify-content: center;
    align-items: center;

}

.title span {
    color: aqua;
    padding-left: 15px;
}

.reset{
    width: 250px;
    height: 100px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 50px;
    background: #204152;
    font-size:  26px;
    color:aqua;
    margin-top: 25px;
    margin-bottom: 50px;
}

.boxes{
    display: flex;
    height: 180px;
    width: 180px;
    background: #204152;
    border: 4px solid #17313d;
    border-radius: 12px;
    cursor: pointer;

}

.board{
    height: 600px;
    width: 564px;
    display: flex;
    margin: auto;
}
